<template lang="">
  <div class="weather_app">
    <SearchView @searchQuery="searching" :setDefault="setDefault"></SearchView>

    <div
      class="d-flex justify-content-center align-items-center not_found text-center"
      v-if="this.noResults == true || this.results.message == 'invalid query'"
    >
      <h2>No Results Found</h2>
    </div>
    

    <!--<div
      class="d-flex justify-content-center align-items-center not_found text-center"
      v-if="
        this.connectionKo == true || this.results.message == 'Network Error'
      "
    >
      <h2>
        Connection error. The database is not responding. <br />
        Please try again later
      </h2>
    </div>-->

    <div class="text-center mt-3 mb-3 welcome" v-if="welcome"></div>

    <div
      v-if="loading"
      class="d-flex justify-content-center align-items-center loading"
    >
      <swapping-squares-spinner
        :animation-duration="1000"
        :size="65"
        color="#e88244"
      />
    </div>

    <div
      class="weatherdb"
      v-if="this.found == true && this.results.message != 'invalid query'"
    >
      <div class="item">
        <div class="content">
          <div class="day">
            <span class="region">Now in {{ results.region }}</span>
          </div>
          <div class="main">
            <div class="w_img">
              <div class="icon">
                <img
                  v-if="today.iconURL"
                  class="img-fluid"
                  :src="today.iconURL"
                  :alt="today.comment"
                />
                <img
                  v-else
                  class="img-fluid"
                  :src="require('../assets/img/Weather_db.png')"
                />
              </div>
              <div class="degree">
                <p>{{ todayTemp.c }}<span>°C</span></p>
                <p>{{ todayTemp.f }}<span>°F</span></p>
              </div>
            </div>
            <div class="info">
              <div class="comment">
                <span>{{ today.comment }}</span>
              </div>
              <div class="stats">
                <div class="stat wind">
                  <span><i class="fa-solid fa-wind"></i> Wind:</span
                  ><span>{{ todayWinds.km }} km/h</span>
                </div>
                <div class="stat humidity">
                  <span><i class="fa-solid fa-droplet"></i> Humidity:</span
                  ><span>{{ today.humidity }}</span>
                </div>
                <div class="stat rain">
                  <span><i class="fa-solid fa-umbrella"></i> Rain:</span
                  ><span>{{ today.precip }}</span>
                </div>
                <div class="stat dayhour">
                  <span><i class="fa-regular fa-clock"></i> Last Update:</span
                  ><span>{{ today.dayhour }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="item next" v-for="day in nextDays" :key="day.id">
        <div class="next_content">
          <div class="next_img">
            <span class="day_day">{{ day.day }}</span>
            <img
              v-if="day.iconURL"
              class="img-fluid"
              :src="day.iconURL"
              :alt="day.comment"
            />
            <img
              v-else
              class="img-fluid"
              :src="require('../assets/img/Weather_db.png')"
            />
          </div>
          <div class="next_comment">
            <span>{{ day.comment }}</span>
          </div>
          <div class="next_temp">
            <div class="max">
              <div class="icon">
                <i class="fa-solid fa-arrow-trend-up"></i>
              </div>
              <div
                class="degree"
                v-for="(value, key) in day.max_temp"
                :key="key.id"
              >
                <p>{{ value }} {{ key }}°</p>
              </div>
            </div>
            <div class="min">
              <div class="icon">
                <i class="fa-solid fa-arrow-trend-down"></i>
              </div>
              <div
                class="degree"
                v-for="(value, key) in day.min_temp"
                :key="key.id"
              >
                <p>{{ value }}° {{ key }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import SearchView from "../components/WeatherApp/Search.vue";
import { SwappingSquaresSpinner } from "epic-spinners";
export default {
  name: "WeatherView",
  components: {
    SearchView,
    SwappingSquaresSpinner,
  },
  data() {
    return {
      apiQuery: "",
      apiWeatherURL: "https://weatherdbi.herokuapp.com/data/weather/",
      results: {},
      today: {},
      todayWinds: {},
      todayTemp: {},
      nextDays: [],
      nextDaysMax: {},
      welcome: true,
      noResults: false,
      found: false,
      loading: false,
      //connectionKo: false,
    };
  },
  methods: {
    // funzione che permette di eseguire la chiamata API avviando la ricerca di query(del testo inserito dall'utente)
    searching(apiQuery) {
      if (apiQuery != "") {
        this.noResults = false;
        this.loading = true;
        this.apiQuery = apiQuery;
        //this.connectionKo = false;
        this.welcome = false;
        this.found = false;
        axios
          .get(this.apiWeatherURL + this.apiQuery)
          .then((resp) => {
            this.results = resp.data;
            //console.log(this.results);
            this.today = this.results.currentConditions;
            //console.log(this.today);
            this.todayWinds = this.today.wind;
            //console.log(this.todayWinds);
            this.todayTemp = this.today.temp;
            //console.log(this.todayTemp);
            this.nextDays = this.results.next_days.filter((x, y) => y != 0);
            console.log(this.nextDays);
            this.loading = false;
            if (this.results.length === 0) {
              this.noResults = true;
            } else {
              this.found = true;
            }
          })
          .catch((error) => {
            this.loading = false;
            //this.noResults = true;
            this.connectionKo = true;
            console.log(error);
          });
      }
    },
    //funzione per resettare gli array
    reset() {
      this.loading = false;
      this.found = false;
      this.welcome = true;
      this.results = [];
    },
    //funzione per la situazione di default,viene passata come prop per il bottone reset
    setDefault() {
      this.reset();
      //this.popular();
    },
    substringText(string, min, max) {
      return string.substring(min, max);
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/style/app.scss";
.weather_app {
  min-height: 100vh;
  background-color: #32485e;
  padding: 0px 0px 50px 0px;
  color: $color-main;
  @media all and (max-width: 992px) {
    padding: 0px 0px 50px 0px;
  }
  h1 {
    color: $color-main;
    text-align: center;
  }
}
.not_found {
  height: 75vh;
  color: #fff;
}
.loading {
  height: 75vh;
}
.welcome {
  background: url(../assets/img/weather-icons.gif);
  background-position: center;
  background-repeat: no-repeat;
  height: 50vh;
}
.weatherdb {
  color: #000000;
  border-top: 3px solid #32485e;
  @include flex($justify-content: space-between, $flex-direction: column);
  .item {
    height: 200px;
    width: 100%;
    overflow: hidden;
    background-image: url(../assets/img/web_desktop.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid white;
    @include flex($justify-content: center, $align-items: center);
    .content {
      height: 200px;
      width: 100%;
      background: #0c3253a9;
      .day {
        height: 10%;
        width: 100%;
        color: #000000;
        background-color: rgba(245, 245, 245, 0.414);
        padding: 1px;
        @include flex($justify-content: center, $align-items: center);
        span {
          font-size: 0.8rem;
          font-weight: 600;
          letter-spacing: 1%;
          text-transform: uppercase;
          @media all and (max-width: 990px) {
            padding-left: 2px;
            font-size: 0.6rem;
            letter-spacing: 0.5%;
          }
        }
        .region {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          color: rgb(0, 0, 0);

          text-shadow: 3px 2px 3px rgba(255, 255, 255, 0.2);
        }
      }
      .main {
        @include flex($justify-content: flex-start, $align-items: center);
        height: 90%;
        div {
          height: 100%;
          //background: #00d7b2;
          border: 1px solid transparent;
          &.w_img {
            height: 90%;
            width: 40%;
            border-right: 1px solid #00b6fb;
            @include flex($flex-direction: column);
            .icon {
              @include flex($justify-content: center, $align-items: center);
              img {
                width: 100px;
                @media all and (max-width: 330px) {
                  width: 150px;
                }
              }
            }
            .degree {
              @include flex($align-items: center, $flex-direction: column);
              color: rgb(255, 255, 255);
            }
          }
          &.info {
            @include flex($flex-direction: column);
            height: 90%;
            width: 60%;
            .comment {
              @include flex($justify-content: center, $align-items: center);
              height: 30%;
              background: #1968a47a;
              span {
                font-size: 1.5rem;
                font-weight: 600;
                letter-spacing: 1%;
                text-transform: uppercase;
                color: rgb(255, 255, 255);
                @media all and (max-width: 850px) {
                  font-size: 0.9rem;
                }
                @media all and (max-width: 350px) {
                  font-size: 0.8rem;
                }
              }
            }
            .stats {
              @include flex(
                $justify-content: center,
                $align-items: center,
                $flex-direction: column
              );
              height: 70%;
              div {
                width: 80%;
                height: calc(100% / 4);
                &.stat {
                  border-bottom: 1px solid #6a8ba47a;
                  color: #fff;
                  @include flex(
                    $justify-content: space-between,
                    $align-items: center
                  );
                  span {
                    @media all and (max-width: 550px) {
                      font-size: 0.6rem;
                    }
                    @media all and (max-width: 350px) {
                      font-size: 0.4rem;
                    }
                    i {
                      color: #00b6fb;
                      width: 25px;
                      @media all and (max-width: 550px) {
                        width: 15px;
                      }
                      @media all and (max-width: 350px) {
                        display: none;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    &.next {
      height: calc(100vh / 7);
      min-height: 100px;
      background-image: url(../assets/img/w_app.png);
      background-repeat: no-repeat;
      .next_content {
        height: 100%;
        width: 100%;
        background: rgba(15, 44, 65, 0.684);
        @include flex($justify-content: center, $align-items: center);
        div {
          @include flex($justify-content: center, $align-items: center);
          height: 100%;
          &.next_img {
            background: rgba(25, 104, 164, 0.4784313725);
            @include flex($flex-direction: column);
            width: 40%;
            height: 100%;
            span {
              color: #fff;
              font-size: 0.8rem;
              font-weight: 600;
              letter-spacing: 1%;
              text-transform: uppercase;
              @media all and (max-width: 990px) {
                padding-left: 2px;
                font-size: 0.6rem;
                letter-spacing: 0.5%;
              }
            }
            img {
              height: 60%;
            }
          }
          &.next_comment {
            background: rgba(25, 104, 164, 0.4784313725);
            width: 10%;
            height: 100%;
            text-align: center;
            span {
              font-size: 1.2rem;
              font-weight: 600;
              letter-spacing: 1%;
              text-transform: uppercase;
              color: rgb(255, 255, 255);
              @media all and (max-width: 1000px) {
                font-size: 0.6rem;
              }
              @media all and (max-width: 500px) {
                font-size: 0.4rem;
              }
              @media all and (max-width: 350px) {
                display: none;
              }
            }
          }
          &.next_temp {
            background: rgba(25, 104, 164, 0.4784313725);
            width: 50%;
            color: #fff;
            @include flex($justify-content: space-evenly, $align-items: center);
            .max,
            .min {
              @include flex($flex-direction: column, $align-items: center);
              @media all and (max-width: 350px) {
                font-size: 0.6rem;
              }
            }
            .max {
              i {
                color: coral;
              }
            }
            .min {
              i {
                color: deepskyblue;
              }
            }
          }
        }
      }
    }
  }
}
</style>
